.element-cube {
  border: 1px solid rgba(255, 255, 255, .3);
  width: 65px;
  height: 65px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  cursor: pointer;
  -webkit-transition: all .05s;
  -o-transition: all .05s;
  transition: all .05s;
}

.element-cube:hover {
  -webkit-transform: scale(1.25);
      -ms-transform: scale(1.25);
          transform: scale(1.25);
}

.element-cube p {
  line-height: 1.15;
  margin-bottom: 0;
}

.element-cube .number {
  font-size: .6rem;
}

.element-cube .symbol {
  font-size: 1.25rem;
}

.element-cube .name {
  font-size: .6rem;
}

.element-cube .atomic-mass {
  font-size: .6rem;
}



